@keyframes slide {
    0% {
        transform: translateY(0%);
    }
    100% {
        transform: translateY(-100%);
    }
}


.loading-haus {
    width: 50px;
    height: 50px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    animation: loading .7s forwards;
    z-index: 999;
    img {
        max-width: 100%;
    }
}


.grid__item {
    display: flex;
    justify-content: center;
    align-items: center;
}

@keyframes loading {
    0% {
       opacity: 1;
    }
    99% {
        opacity: 1;
     }
    100% {
        opacity: 0;
    }
}